.carouselExampleIndicators {
    width: 100%;
    margin: 0 auto;
    cursor: pointer;
   
  }
  
  .carousel-indicators{
    transform: translateY(30px);
    width: 10%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 0 auto;
    cursor: pointer;
  }
  .carousel-item  {
    display: none;
  }
  
  @media screen and (max-width: 420px) {
  
    .w-100{
      width: 200%!important;
    }
  
  }
  
  
  @media screen and (max-width: 260px) {
  
    .w-100{
      width: 220%!important;
    }
  }
    @media screen and (max-width: 200px) {
  
      .carouselSection{
       display: none;
      }
    
    }
  
  