  top: 0;
  z-index: 50;
  background-color: #fff;
  box-sizing: border-box;
}

/* LOGO STYLE */
.logo a {
  text-decoration: none;
  color:black;
  
}
.logo a h4 {
  font-size: 35px;
  font-weight: 450;
  letter-spacing: 4px;
  cursor: pointer;
  -webkit-filter: drop-shadow(1px 5px 2px rgb(218, 216, 216));
          filter: drop-shadow(1px 5px 2px rgb(218, 216, 216));
  /* background-color: red; */
}
.logo a h4 span {
  color: #009688;
  letter-spacing: 1px;
  font-weight: 500;
}

/* NAVBAR STYLE */
.nav-links {
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 35%;
  margin-left: 5%;
}
.nav-links li a {
  font-size: 16px;
  color: rgba(27, 25, 25, 0.849);
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 2px;
  transition: all 0.2s;
  -webkit-filter: drop-shadow(1px 5px 2px rgb(218, 216, 216));
          filter: drop-shadow(1px 5px 2px rgb(218, 216, 216));
}
.nav-links li a:hover {
  color: #009688;
  text-decoration: unset;
}

/* ICONS STYLE */
.icons {
  width: 20%;
  display: flex;
  color: rgb(27, 25, 25);
  justify-content: space-evenly;
  align-items: flex-start;
  margin-top: 0.5%;
  -webkit-filter: drop-shadow(1px 5px 2px rgb(218, 216, 216));
          filter: drop-shadow(1px 5px 2px rgb(218, 216, 216));

}
.icons i {
  font-size: 28px;
  cursor: pointer;
  color: rgb(27, 25, 25);
}
.cart-count {
  color: #fff;
  background-color: #009688;
  border-radius: 15px;
  font-size: 15px;
  padding: 5% 20%;
}
sup{
