.features {
    display: flex;
    justify-content: space-around;
    margin-top: 3%;
  }
  /* Icon Container */
  .features .icon-container {
    width: 20%;
    min-height: 200px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  /* Image Wrapper */
  .features .img-wrapper {
    cursor: pointer;
    padding: 15px;
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background-color: #009688;
    animation: animate 3s linear infinite;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  /* Image Wrapper Image */
  .features .img-wrapper img {
    width: 40px;
    height: 38px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  /* Icon Container h4 */
  .features .icon-container h4 {
    font-size: 18px;
    color: rgb(27, 25, 25);
    position: absolute;
    top: 80%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Montserrat", sans-serif;
  }
  /* Animation */
  @keyframes animate {
    0% {
      box-shadow: 0 0 0 0 rgba(0, 150, 136, 0.7), 0 0 0 0 rgba(0, 150, 136, 0.7);
    }
    40% {
      box-shadow: 0 0 0 50px rgba(0, 150, 136, 0), 0 0 0 0 rgba(0, 150, 136, 0.7);
    }
    80% {
      box-shadow: 0 0 0 50px rgba(0, 150, 136, 0), 0 0 0 30px rgba(0, 150, 136, 0);
    }
    100% {
      box-shadow: 0 0 0 0 rgba(0, 150, 136, 0), 0 0 0 30px rgba(0, 150, 136, 0);
    }
  }
  
  
  @media screen and (max-width: 840px) {
    .features .icon-container h4 {
      font-size: 14px;
