.order-confirm {
    text-align: center;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .order-confirm img {
    width: 300px;
    margin-top: 7%;
  }
  .order-confirm h3 {
    font-weight: bold;
  }
  .order-confirm p {
    margin-top: 0.5%;
  }