@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;1,200;1,300;1,400;1,500;1,600&display=swap");
.mainP {
  transform: scale(1.27, 1.25);
  margin-top: 19%;
  max-height: 380px;
}
.smallImg {
  cursor: pointer;
}
.mysection {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 2% 8%;
  margin-top: 5%;
  box-sizing: border-box;
}
.leftDiv {
  width: 35%;
  padding-right: 8%;
  box-sizing: border-box;
  align-self: flex-start;
}
.rightDiv {
  align-self: flex-start;
  width: 55%;
  box-sizing: border-box;
  z-index: 5;
}
.rightDiv > div:nth-of-type(1) {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  /* margin-bottom: 40px; */
}
h3 {
  font-family: "Montserrat", sans-serif;
  margin: 0;
  padding: 0;
  margin-top: 5%;
  font-weight: 200;
  font-size: 20px;
  letter-spacing: 1px;
  color: rgb(27, 25, 25);
}
.rightDiv h1 {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 36px;
  letter-spacing: -2px;
  margin: 0;
  padding: 0;
  letter-spacing: 0px;
}
.rightDiv > div > h4 {
  font-weight: 200;
  font-size: 19px;
  letter-spacing: 1px;
  color: rgb(27, 25, 25);
  margin: 0;
  margin-top: 1%;
  font-family: "Montserrat", sans-serif;
}
.rightDiv > div > div > p {
  font-size: 16px;
  font-weight: 300;
  color: #86939e;
}
h3 span:nth-of-type(2) {
  font-size: 20px;
  letter-spacing: 1px;
  color: #009688;
  font-weight: 500;
}
.mainP {
  width: 100%;
  box-sizing: border-box;
}
#btn-add-to-cart {
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  padding: 10px 20px;
  letter-spacing: 2px;
  background-color: #009688;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0 2px 10px rgb(0 0 0 / 50%);
  margin: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  margin-top: 5%;
}

.borderBox {
  border: 2px solid green;
}
.borderImage{
  padding:1.5%;
  width: 80px;
  border-radius: 7px;
}
.SIcontainer{
  margin-top: 5%;
  flex-wrap: wrap;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
}



@media screen and (max-width: 900px) {
  .mysection {
    margin-top: 9%;
  }
  .rightDiv > div > div > p {
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
  .rightDiv h1 {
    font-size: 30px;
}


@media screen and (max-width: 780px) {
  .mainP {
    transform: scale(1.27, 1.55);
    margin-top: 50%;
    max-height: unset;
  }
  .rightDiv h1 {
    font-size: 30px;
  }
}



@media screen and (max-width: 730px) {
  .SIcontainer{
    width: 80%;
  }
}
}
@media screen and (max-width: 540px) {
  .mainP {
    transform: scale(1, 1);
    margin-top: unset;
    max-height: unset;
  }
  .rightDiv {
    width: 100%;
  }
  .leftDiv {
    width: 100%;
    display: block;
    padding: unset;
    padding: 5%;
  }
  .mysection {
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 0 auto;
    margin-top: 15%;
    box-sizing: border-box;
  }
  #btn-add-to-cart{
    margin-left: 25%;
  }
}
@media screen and (max-width: 330px) {
  #btn-add-to-cart{
    margin-left:unset;
  }
}