.top-bar {
  width: 100vw;
  display: flex;
  justify-content: space-evenly;
  box-shadow: 2px 4px 2px rgba(17, 16, 16, 0.5);
  padding: 1.5% 3%;
  padding-bottom: 0.5%;
  position: fixed;
  top: 0;
  z-index: 50;
  background-color: #fff;
  box-sizing: border-box;
}

/* LOGO STYLE */
.logo a {
  text-decoration: none;
  color:black;
  
}
.logo a h4 {
  font-size: 35px;
  font-weight: 450;
  letter-spacing: 4px;
  cursor: pointer;
  filter: drop-shadow(1px 5px 2px rgb(218, 216, 216));
  /* background-color: red; */
}
.logo a h4 span {
  color: #009688;
  letter-spacing: 1px;
  font-weight: 500;
}

/* NAVBAR STYLE */
.nav-links {
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 35%;
  margin-left: 5%;
}
.nav-links li a {
  font-size: 16px;
  color: rgba(27, 25, 25, 0.849);
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 2px;
  transition: all 0.2s;
  filter: drop-shadow(1px 5px 2px rgb(218, 216, 216));
}
.nav-links li a:hover {
  color: #009688;
  text-decoration: unset;
}

/* ICONS STYLE */
.icons {
  width: 20%;
  display: flex;
  color: rgb(27, 25, 25);
  justify-content: space-evenly;
  align-items: flex-start;
  margin-top: 0.5%;
  filter: drop-shadow(1px 5px 2px rgb(218, 216, 216));

}
.icons i {
  font-size: 28px;
  cursor: pointer;
  color: rgb(27, 25, 25);
}
.cart-count {
  color: #fff;
  background-color: #009688;
  border-radius: 15px;
  font-size: 15px;
  padding: 5% 20%;
}
sup{
  top: -1.2em;
  right:0.5em;
}
.burger {
  display: none;
  cursor: pointer;
}
.burger div {
  width: 25px;
  height: 3px;
  background-color: rgb(27, 25, 25);
  margin: 5px;
  transition: all 0.3s ease;
}

/* BURGER STYLE */
.toggle .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}
.toggle .line2 {
  opacity: 0;
}
.toggle .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}


@media screen and (max-width: 1020px) {
  .nav-links{
  width: 40%;

  }
  .nav-links li a{
    font-size: 16px;
    letter-spacing: 0;
  }
  .logo a h4{
    font-size: 26px;
  }
  .icons i {
    font-size: 20px;
  }
  .cart-count{
    font-size: 10px;
  }
}



@media screen and (max-width: 700px) {
  .nav-links{
    width: 50%;
    }
    .nav-links li a{
      font-size: 12px;
      letter-spacing: 0;
    }
    .logo a h4{
      font-size: 22px;
    }
  }







/* NAVBAR RESPONSIVE STYLE */
@media screen and (max-width: 530px) {
  body {
    overflow-x: hidden;
  }
  .top-bar{
   display: unset;
   display: flex;
   justify-content: space-around;
  }
  .nav-links {
    position: absolute;
    right: 0;
    height: 89vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 50%;
    transform: translateX(100%);
    transition: 0.5s ease-in;
    background: #009688;
    z-index: 100;
  }
  .nav-links li {
    opacity: 1;
    margin-top: 100px;
  }
  .nav-links li a {
    color: rgb(27, 25, 25);
  }
  .nav-links li a:hover {
    color: #fff;
  }
  .burger{
    display: block;
  }
   .icons{
    width: 50%;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
} 
}

@media screen and (max-width: 425px) {
  body {
    overflow-x: hidden;
  }
  .logo a h4 {
    margin-top: 3%;
    font-size: 18px;
  }
  .icons i {
    padding-top: 6px;
    font-size: 16px;
    margin-right: 20px;
  }

  .burger {
    padding-top: 0px;
  }
.burger div {
    width: 20px;
    height: 2px;
}
}
@media screen and (max-width: 320px) {
  .logo a h4 {
    font-size: 20px;
  }
  .icons i {
    padding-top: 4px;
    font-size: 16px;
    margin-right: 15px;
  }
  .fa-search{
    display: none;
  }
  .burger {
    padding-top: 0px;
    width: 1px;
    height: 1px;
    padding-right: 15%;
  }
}

@media screen and (max-width: 250px) {
  .logo a h4 {
    font-size: 16px;
    padding-right: 20%;
    /* color: red; */
    letter-spacing: unset;
  }

  .icons i {
    padding-top: 4px;
    font-size: 12px;
    margin-right: 15px;
  }
  
  .burger div {
    padding-top: 0px;
    width: 15px;
    height: 0.1px;
    padding-right: 15%;
  }
}

/* NAVBAR ACTIVE STYLE */
.nav-active {
  transform: translateX(0%) translateY(6%);
}

